  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  transition: background-color 0.3s linear;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 80%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  margin: 0 auto;
}

.w-100 {
  width: 100%;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  transition: all 0.3s ease;
  display: block;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #be7208;
  cursor: pointer;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  transition: all 0.3s ease;
  display: inline-block;
  border: none;
}
.boton-amarillo:hover {
  background-color: #be7208;
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  transition: all 0.3s ease;
  display: inline-block;
  border: none;
}
.boton-verde:hover {
  background-color: #5a8d00;
  cursor: pointer;
}

.boton-verde-block {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  transition: all 0.3s ease;
  display: block;
  border: none;
}
.boton-verde-block:hover {
  background-color: #5a8d00;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: rgb(206, 7, 7);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  transition: all 0.3s ease;
  display: block;
  border: none;
}
.boton-rojo-block:hover {
  background-color: #ab0606;
  cursor: pointer;
}

.boton-return {
  display: flex;
  justify-content: center;
  width: 90%;
  max-width: 50rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .boton-return {
    width: 30rem;
    margin: 0;
  }
}
.boton-return::before {
  content: "";
  display: inline-flex;
  align-items: center;
  width: 3rem;
  height: 3rem;
  background-image: url("../img/icono_return.svg");
  background-repeat: no-repeat;
  background-size: contain;
  padding-right: 4rem;
  filter: invert(100%);
}

.boton-add {
  display: flex;
  justify-content: center;
  width: 90%;
  max-width: 50rem;
  margin: 0 auto;
}
.boton-add::before {
  content: "";
  width: 3rem;
  height: 3rem;
  background-image: url("../img/icon_add.svg");
  background-repeat: no-repeat;
  background-size: contain;
  padding-right: 4rem;
  filter: invert(100%);
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p, .dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode td, .dark-mode th {
  color: #FFFFFF;
}
.dark-mode .anuncio {
  background-color: #333333;
  border: 1px solid #4d4d4d;
}
.dark-mode .barra-nav .dark-mode-button {
  transition: filter 0.3s linear;
  filter: invert(0%);
}
.dark-mode .iconos-caracteristicas img {
  filter: invert(100%);
}
.dark-mode .formulario legend,
.dark-mode .formulario label,
.dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  color: #FFFFFF;
}

/* UI */
.no-webp .inicio {
  background-image: url("../img/header.jpg");
}

.webp .inicio {
  background-image: url("../img/header.webp");
}

.header {
  background-color: #333333;
}
.header.inicio {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 100vh;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 100vh;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  color: #FFFFFF;
  text-align: left;
  max-width: 60rem;
  line-height: 2;
  font-weight: 700;
}

.barra {
  display: flex;
  flex-direction: column;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  visibility: hidden;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s ease;
  opacity: 0;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    visibility: visible;
    opacity: 1;
  }
}
.navegacion a {
  display: block;
  color: #FFFFFF;
  text-align: center;
  font-size: 2.2rem;
  transition: color 0.3s ease;
}
.navegacion a:hover {
  color: #71B100;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
.navegacion.show {
  visibility: visible;
  opacity: 1;
  height: auto;
}

/* dark mode button */
.barra-nav {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .barra-nav {
    flex-direction: row;
  }
}
.barra-nav .dark-mode-button {
  width: 4rem;
  filter: invert(100%);
  transition: filter 0.3s linear;
  cursor: pointer;
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .barra-nav .dark-mode-button {
    margin: 0 3rem 0 0;
  }
}

/* Menu de hamburguesa */
.barra-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .barra-menu {
    display: block;
  }
}

.mobile-menu {
  padding: 0.3rem 1rem;
  border-radius: 2rem;
  transition: background-color 0.3s ease;
}
.mobile-menu:hover {
  background-color: rgba(225, 225, 225, 0.4392156863);
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
  cursor: pointer;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}
.icono p {
  text-align: center;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.anuncio {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .anuncio {
    margin-bottom: 0;
  }
}
.anuncio .contenido-anuncio {
  text-align: center;
  padding: 2rem;
}
.anuncio h3, .anuncio p {
  margin: 0;
}
.anuncio .precio {
  color: #71B100;
  font-weight: 700;
  text-align: center;
  font-size: 3rem;
  margin: 2rem 0;
}

.iconos-caracteristicas {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
  padding: 0;
  list-style: none;
  text-align: center;
  max-width: 40rem;
  margin: 0 auto;
}
.iconos-caracteristicas li {
  display: flex;
  justify-content: center;
}
.iconos-caracteristicas li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.iconos-caracteristicas li p {
  font-weight: 900;
}

.ver-todas {
  display: flex;
  justify-content: center;
}

.no-webp .imagen-contacto {
  background-image: url("../img/encuentra.jpg");
}

.webp .imagen-contacto {
  background-image: url("../img/encuentra.webp");
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  color: #FFFFFF;
  font-size: 4rem;
  font-weight: 900;
}
.imagen-contacto p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}

.entrada-blog {
  margin-bottom: 3rem;
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}

.texto-entrada a {
  color: #000000;
  display: block;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
}
.texto-entrada span {
  color: #E08709;
}

.testimonial {
  padding: 2rem;
  background-color: #71B100;
  border-radius: 2rem;
  color: #FFFFFF;
  font-size: 2.4rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 5rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  color: #FFFFFF;
  text-align: right;
}

.formulario p {
  font-size: 1.5rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}
.formulario .submit-button {
  display: flex;
  justify-content: center;
}
.formulario .submit-button input {
  width: 50%;
  border-radius: 2rem;
  text-transform: uppercase;
  letter-spacing: 0.5rem;
}
.formulario img {
  width: 30rem;
  margin: 1rem 0;
}

.footer {
  background-color: #333333;
  padding: 2.5rem 0;
  margin-bottom: 0;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.contenedor-footer {
  padding-top: 2.5rem;
}

.copyright {
  color: #FFFFFF;
  text-align: center;
}

/* Internas */
@media (min-width: 768px) {
  .nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.nosotros-contenido p:first-of-type {
  font-weight: 900;
  font-size: 2.2rem;
  margin-bottom: 3rem;
}

.main-admin {
  min-height: 60vh;
}

.alerta {
  border-radius: 1rem;
  margin: 1rem auto;
  color: #FFFFFF;
  text-align: center;
  text-transform: uppercase;
  font-size: 1.5rem;
  padding: 1rem;
}
.alerta.error {
  background-color: rgb(206, 7, 7);
}
.alerta.exito {
  background-color: #71B100;
  animation: desaparecer 2s forwards 3s;
}

@keyframes desaparecer {
  to {
    opacity: 0;
    display: none;
  }
}
.propiedades {
  margin: 2rem auto;
  width: 100%;
  border-spacing: 0;
}
.propiedades thead {
  background-color: #71B100;
}
.propiedades thead th {
  color: #FFFFFF;
  padding: 2rem;
}
.propiedades tbody td {
  text-align: center;
  padding-bottom: 2rem;
}

.administrar-container {
  margin-bottom: 2rem;
}
.administrar-container p {
  margin: 0;
}

.img-small {
  width: 20rem;
  padding: 1rem;
}

.login {
  width: 50rem;
}

.admin-img {
  width: 30rem;
  margin: 0 auto;
}